<template>
  <div class="article">
    <van-skeleton
            title
            :row="8"
            :loading="loadingSkeleton" style="margin-top: 8vw">
      <div class="article-title">
        {{detail.article_title}}
      </div>
      <van-collapse v-model="activeNames">
        <van-collapse-item title="原文" name="1" size="large">
          <div class="article-content" v-html="detail.article_content"></div>
        </van-collapse-item>
        <van-collapse-item title="翻译" name="2" size="large">
          <div class="article-translate" v-html="detail.article_translate"></div>
        </van-collapse-item>
        <van-collapse-item title="解析" name="3" size="large">
          <div v-html="detail.article_analysis"></div>
        </van-collapse-item>
      </van-collapse>
    </van-skeleton>
  </div>
</template>
<script>
  import {mapGetters} from "vuex";
  import { Collapse, CollapseItem, Skeleton } from 'vant';
  export default {
    name: 'payedArticle',
    computed: {
      ...mapGetters(["userInfo"])
    },
    data() {
      return {
        orderId: '',
        loadingSkeleton: true,
        activeNames: ['1'],
        detailParams: {
          orderId: '',
          openid: '',
          payed: 1 // 已支付的文章
        },
        detail: '',
        loadingBtn: false
      }
    },
    components: {
      [Skeleton.name]: Skeleton,
      [Collapse.name]: Collapse,
      [CollapseItem.name]: CollapseItem,
    },
    created() {
      this.detailParams.openid = this.userInfo.openid;
      this.detailParams.orderId = this.$route.params.id;
      console.log(this.orderId)
    },
    mounted() {
      this.articleDetail();
     // this.payParams.openId = localStorage.getItem('openid')
    },
    methods: {
      articleDetail() {
        this.apis.order.articleDetail(this.detailParams).then(res => {
          this.detail = res.data.article;
          this.loadingSkeleton = false;
        })
      }
    }
  }
</script>
<style lang="less">
  .article{
    margin-bottom: 20vw;
    text-align: left;
    .article-title {
      width: 100%;
      padding: 5vw 3vw;
      font-size: 4.2vw;
      font-weight: 600;
    }
    .article-content {
      width: 100%;
      /*border-radius: 2vw;*/
      /*padding: 1vw 2vw;*/
      /*border: 1px solid #1cb5cc;*/
    }
    .article-translate {

    }
  }
</style>